.main {
  position: relative;
  height: 50px;
  border-radius: 5px;
  border: 1.5px solid rgb(7, 4, 4);
  overflow: hidden;
  margin-bottom: 20px;
  margin-top: 40px;
}

.fill {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(98, 252, 60);
}

